<template>
    <section>
         <el-row>
            <el-col :span ='24' style='margin-top:20px;'>
                    <el-col :span='20'>
                            <span>本页显示 
                            <el-select v-model="value" placeholder="5" style='width:80px'>
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                            </el-select> 条</span>
                    </el-col>

                    <el-col :span='4' align='right'>
                        <el-button type="primary" @click="dialogFormVisible = true">上传图片</el-button>
                    </el-col>
                    <el-col :span='24'>
                        <el-dialog  :visible.sync="dialogFormVisible" >
                            <el-form :model="form">
                                <el-form-item label="请选择图片：" :label-width="formLabelWidth" align='left'>
                                    <el-upload
                                        action="http://118.89.232.160:10001/util/file/upload.json"
                                        :file-list="fileList"
                                        list-type='picture'
                                        on-success='handleAvatarSuccess'>
                                        <el-button size="small" type="primary">选择文件</el-button>
                                    </el-upload>
                                </el-form-item>
                                <el-form-item label="跳转地址：" :label-width="formLabelWidth" align='left'>
                                    <el-input  v-model='form.jump_url'  style='width:300px;'></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer" align='center'>
                                <el-button type="primary" @click="bindimgs">绑 定</el-button>
                            </div>  
                        </el-dialog>
                    </el-col>   
            </el-col>
            <el-col :span='24' style='margin-top:20px'>
                    <el-table
                        :data="tableData"
                        style="width: 100%;">
                        <el-table-column
                        prop="date"
                        label="图片"
                        width="200"
                        align='center'>
                        </el-table-column>
                        <el-table-column
                        prop="name"
                        label="url地址"
                        width="600"
                        align='center'>
                        </el-table-column>
                        <el-table-column
                        prop="address"
                        label="操作"
                        align='center'>
                        </el-table-column>
                    </el-table>
            </el-col>

            <el-col :span='24' align='right'>
                    <div class="block">
                        <el-pagination
                        layout="prev, pager, next"
                        :total="50">
                        </el-pagination>
                    </div>
            </el-col>
        </el-row>
    </section>
</template>

<script>
import { updataimgs,requestUpload} from '../../../api/display'
export default {
    data() {
      return {
        dialogFormVisible: false,
        form: {
          pic_url: '',
          jump_url: '',
          uid:'1209811640320002'
        },
        formLabelWidth: '100px',
        fileList:[],
        options: [{
          value: '5',
          label: '5'
        }, {
          value: '10',
          label: '10'
        }],
        value: ''
      };
    },
    methods:{
       handleAvatarSuccess (res, file) {
         this.imageUrl = URL.createObjectURL(file.raw);
         console.log(this.imageUrl)
            const formData = new FormData();
            formData.append('pic',file.raw);
            formData.append('type','image');
            requestUpload(formData).then(data => {
                let { error_code, result } = data;
                if (error_code !== 0) {
                    this.$message({
                        message: "返回数据有误",
                        type: 'error'
                    });
                } else {
                    this.imageUrl = data.result.original_pic
            }
          })
        },
        bindimgs(){
            this.dialogFormVisible = false
            let str = {
                pic_url:'img.png',
                jump_ur:'www.baidu.com',
                uid:'1209811640320002'
            }
            updataimgs(str).then(data => {   
                console.log(data)
            })
        }
	}
};
</script>


